.info-container::after {
    content: "";
    clear: both;
    display: table;
  }
  
.about-container {
width: 50%;
height: auto;
float: left;
position: relative;
}

.about-img {
max-width: 100%;
height: auto;
}

.donate-container {
width: 50%;
height: auto;
float: right;
position: relative;
}

.donate-img {
max-width: 100%;
height: auto;
}

.link-btn-container-left {
    border: 2px solid white;
    height: 50px;
    width: 200px;
    position: absolute;
    top: 15%;
    left: 40%;
}

.link-btn-container-right {
    border: 2px solid white;
    height: 50px;
    width: 200px;
    position: absolute;
    top: 15%;
    right: 40%;
}

@media only screen and (max-width: 700px) {
    .info-container {
        content: "";
        clear: both;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      
    .about-container {
        width: 100%;
        height: auto;
    }
    
    .about-img {
        max-width: 100%;
        height: auto;
    }
    
    .donate-container {
        width: 100%;
        height: auto;
    }
    
    .donate-img {
        max-width: 100%;
        height: auto;
    }

    .link-btn-container-left {
        border: 2px solid white;
        height: 50px;
        width: 200px;
        left: 20%;
        top: 20%;
    }

    .link-btn-container-right {
        border: 2px solid white;
        height: 50px;
        width: 200px;
        left: 20%;
        top: 20%;
    }
    
    .sponsor-footer {
        font-size: 12px;
        max-width: 100%;
        height: auto;
        margin: auto;
        margin-top: 100px;
        margin-bottom: 100px;
        padding: 5px;
        align-content: center;
        text-align: center;
        border: 1px solid black;
      }
}