.donate-info-container {
    background: #3c3b6e;
    color: white;
    width: 50vmax;
    height: 40vmax;
    border: 2px solid white;
    position: absolute;
    top: 40%;
    right: 10%;
  }

  .link-btn-container-2 {
    border: 2px solid white;
    height: 50px;
    width: 200px;
    position: absolute;
    top: 70%;
    left: 10%;
  }

  @media only screen and (max-width: 700px) {
    h3 {
        font-size: 18px;
    }

    .donate-page {
        font-size: 12px;
        display: flex;
        flex-direction: column;
    }

    .donate-info-container {
        background: #3c3b6e;
        color: white;
        width: 100%;
        height: auto;
        border: 2px solid white;
        top: 50%;
        left: 0;
      }

      .link-btn-container-2 {
        border: 2px solid white;
        height: 50px;
        width: 200px;
        top: 30%;
        margin-left: 10vw;
      }

      .sponsor-footer {
        position: absolute;
        font-size: 12px;
        max-width: 100%;
        height: auto;
        bottom: 100%;
        padding: 5px;
        align-content: center;
        text-align: center;
        border: 1px solid black;
    }
  }