.link-btn-container-3 {
    border: 2px solid white;
    height: 50px;
    width: 200px;
    position: absolute;
    top: 40vh;
    right: 15vw;
  }

.about-info-header {
    background: transparent;
    color: white;
    width: 40vmax;
    height: auto;
    position: absolute;
    top: 35vh;
    left: 9vw;
    text-align: left;
    padding: 0.5em;
}

.about-h1 {
    font-size: 72px;
    text-align: right;
    font-style: italic;
}

.about-info-container {
    position: absolute;
    top: 75vh;
    width: 100%;
    height: auto;
}

.about-info-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.about-info-btn-container {
    display: flex;
    border: 2px solid white;
    color: #3c3b6e;
    background: #3c3b6e;
    height: 60px;
    width: 240px;
    margin-left: 2vw;
    margin-right: 2vw;
    margin-top: 2vh;
}

.about-info-btn {
    color: white;
    background: #3c3b6e;
    font-size: 24px;
    border: none;
    width: 100%;
    height: 100%;
}

.about-info-btn:hover {
    background: #3c3b6e;
    border: 2px solid white;
    font-size: 25px;
    color: white;
    opacity: 0.9;
}

@keyframes slideDown {
    0% {
        height: 0;
    }
    100% {
        height: 90vh;

    }
}

@keyframes fadein {
    0% {
        opacity: 0%;
    }
    50% {
        opacity: 0%;
    }
    100% {
        opacity: 100%;
    }
}

.info-pane {
    background: #3c3b6e;
    border: 2px solid white;
    color: white;
    width: 100%;
    font-size: 24px;
    height: 90vh;
    animation: slideDown;
    animation-duration: 2s;

}

.text-container-left {
    width: 40%;
    margin-left: 2em;
    margin-top: 2em;
    text-align: left;
    float: left;
    animation: fadein;
    animation-duration: 2s;
}

.text-container-right {
    width: 40%;
    float: right;
    margin-right: 2em;
    margin-top: 2em;
    text-align: left;
    animation: fadein;
    animation-duration: 2s;
}

.img-left {
    width: 40%;
    margin-left: 2em;
    margin-top: 2em;
    float: left;
    animation: fadein;
    animation-duration: 2s;
}

.img-right {
    width: 40%;
    float: right;
    margin-right: 2em;
    margin-top: 2em;
    animation: fadein;
    animation-duration: 2s;
}

.list-container {
    position: relative;
    width: 30vmax;
    margin: 1vmax;
    margin: auto;
    display: table;
}

ul {
    text-align: left;
    margin-left: 2vmax;
}

@media only screen and (max-width: 700px) {

    .about-info-header {
        background: transparent;
        color: white;
        width: 80%;
        height: auto;
        text-align: left;
        padding: 0.5em;
    }

    .about-h1 {
        font-size: 30px;
        text-align: center;
        color: white;
    }

    .about-info-container {
        top: 50%;
        width: 100%;
        height: auto;
    }

    .about-info-grid {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 20px;
    }

    .about-info-btn-container {
        display: flex;
        border: 2px solid white;
        color: #3c3b6e;
        background: #3c3b6e;
        height: 60px;
        width: 240px;
        margin-left: 2vw;
        margin-right: 2vw;
        margin-top: 2vh;
    }

    .link-btn-container-3 {
        border: 2px solid white;
        height: 50px;
        width: 200px;
        position: absolute;
        top: 20%;
        left: 20%;
      }

    .list-container {
        position: relative;
        width: 100%;
        margin: 1vmax;
        margin: auto;
        display: table;
    }

    .info-pane {
        background: #3c3b6e;
        border: 2px solid white;
        color: #3c3b6e;
        width: 100%;
        font-size: 12px;
        height: auto;
        animation: none;
    }
    
    .text-container-left {
        width: 80%;
        float: left;
        animation: none;
    }
    
    .text-container-right {
        width: 100%;
        text-align: left;
        animation: none;
    }
    
    .img-left {
        width: 100%;
        margin: auto;
        animation: none;
    }
    
    .img-right {
        width: 100%;
        margin: auto;
        animation: none;
    }

    .sponsor-footer {
        position: absolute;
        font-size: 12px;
        max-width: 100%;
        height: auto;
        margin: auto;
        bottom: -130%;
        padding: 5px;
        align-content: center;
        text-align: center;
        border: 1px solid black;
    }
}